import React from 'react';
import '../SignIn/SignIn.css';

class SignUp extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      signUpEmail: '',
      signUpPassword: '',
      signUpName: '',
    };
  }

  onChangeName = (event) => {
    this.setState({ signUpName: event.target.value });
  };

  onChangeEmail = (event) => {
    this.setState({ signUpEmail: event.target.value });
  };

  onChangePassword = (event) => {
    this.setState({ signUpPassword: event.target.value });
  };

  changePageStateOnSubmit = (event) => {
    event.preventDefault();
    this.props.changeUserState({ name: this.state.signUpName });
    if (
      this.state.signUpEmail.length > 0 &&
      this.state.signUpPassword.length > 0
    ) {
      this.props.pageStateChanger('home');
    }
  };

  render() {
    return (
      <article className='br2 ba dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 center shadow-hover'>
        <main className='pa4 black-80'>
          <form className='measure'>
            <fieldset id='sign_up' className='ba b--transparent ph0 mh0'>
              <legend className='main f2 fw6 ph0 mh0'>Registration form</legend>
              <div className='mt3'>
                <label className='db fw6 lh-copy f6' htmlFor='email-address'>
                  Your Name
                </label>
                <input
                  onChange={this.onChangeName}
                  className='pa2 br2 input-reset ba bg-transparent hover-bg-black hover-white w-100'
                  type='text'
                  name='username'
                  id='username'
                />
              </div>
              <div className='mt3'>
                <label className='db fw6 lh-copy f6' htmlFor='email-address'>
                  Email
                </label>
                <input
                  onChange={this.onChangeEmail}
                  className='pa2 br2 input-reset ba bg-transparent hover-bg-black hover-white w-100'
                  type='email'
                  name='email-address'
                  id='email-address'
                />
              </div>
              <div className='mv3'>
                <label className='db fw6 lh-copy f6' htmlFor='password'>
                  Password
                </label>
                <input
                  onChange={this.onChangePassword}
                  className='b pa2 br2 input-reset ba bg-transparent hover-bg-black hover-white w-100'
                  type='password'
                  name='password'
                  id='password'
                />
              </div>
            </fieldset>
            <div className=''>
              <input
                onClick={this.changePageStateOnSubmit}
                className='b br3 ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib'
                type='submit'
                value='Submit'
              />
            </div>
          </form>
        </main>
      </article>
    );
  }
}
export default SignUp;
